import { gsap } from "gsap";

export function startLettersAnimation() {
  let typeSplit = new SplitType(".work_title", {
    types: "words, chars",
    tagName: "span",
  });

  const workLinks = document.querySelectorAll(".work_link");

  Array.from(workLinks).forEach((workLink) => {
    let listOne = workLink.querySelectorAll(".work_title.is-1 .char");
    let listTwo = workLink.querySelectorAll(".work_title.is-2 .char");

    let tl = gsap.timeline({ paused: true });

    tl.to(listOne, {
      translateY: "-0.2em",
      rotationY: "-5.7deg",
      rotationX: "-90deg",
      stagger: { each: 0.08 },
      ease: "power3.inOut",
      duration: 0.7,
    });

    tl.from(
      listTwo,
      {
        translateY: "0.2em",
        rotationY: "5.7deg",
        rotationX: "90deg",
        stagger: { each: 0.08 },
        ease: "power3.inOut",
        duration: 0.7,
      },
      0.1
    );

    workLink.addEventListener("mouseenter", function () {
      tl.restart();
    });

    workLink.addEventListener("mouseleave", function () {
      tl.reverse();
    });
  });
}
